import React, {Component} from 'react';
import PropTypes from 'prop-types';

import './App.css';

import {connect} from 'react-redux';
import {loadDetails} from '../../actions';
import Header from '../Header/Header';
import {Element} from 'react-scroll';
import {ToastContainer} from 'react-toastify';
import ReCAPTCHA from 'react-google-recaptcha';

import About from '../About/About';
import Skills from '../Skills/Skills';
import Experience from '../Experience/Experience';
import Education from '../Education/Education';
import Certification from '../Certification/Certification';
import Connect from '../Connect/Connect';
import Json from '../Json/Json';
import Yaml from '../Yaml/Yaml';

class App extends Component {

	constructor(props) {
		super(props);
    this.state = {
      width: window.innerWidth,
    };
    this.recaptchaRef = React.createRef();
    this.onInvisibleCaptchaChange = this.onInvisibleCaptchaChange.bind(this);
	}

  componentDidMount() {
    setTimeout(()=>{
      this.recaptchaRef.current.execute();
    }, 4000)
  }

  onInvisibleCaptchaChange(captchaToken) {
    this.props.loadDetails(captchaToken)
  }

	render() {
		return (
			<div className={'app'}>
        <Header/>
        <ReCAPTCHA
          ref={this.recaptchaRef}
          size="invisible"
          sitekey="6Ld99q8UAAAAAGLVu4_Or8Y0zn4S0RjYhBO8317M"
          onChange={this.onInvisibleCaptchaChange}
        />
        <div>
          <Element name={'about'}>
            <About/>
          </Element>
          <Element name={'skills'}>
            <Skills/>
          </Element>
          <Element name={'experience'}>
            <Experience/>
          </Element>
          <Element name={'education'}>
            <Education/>
          </Element>
          <Element name={'certification'}>
            <Certification/>
          </Element>
          <Element name={'json'}>
            <Json/>
          </Element>
          <Element name={'yaml'}>
            <Yaml/>
          </Element>
          <Element name={'connect'}>
            <Connect/>
          </Element>
        </div>
        <ToastContainer/>
      </div>
		);
	}
}

App.propTypes = {
  loadDetails: PropTypes.func
};

// function to convert the global state obtained from redux to local props
function mapStateToProps(state) {
	return {
		default: state.default
	};
}

export default connect(mapStateToProps, {loadDetails})(App);
