import axios from 'axios';
import { apiEndpoint } from './parameters';
import types from '../actionTypes';

export const loadDetails = (captchaToken) => {
  return async (dispatch) => {
    try{
      const response = await axios.post(`${apiEndpoint}/get-details`, {
        captchaToken
      });
      dispatch({
        type: types.getDetailsSuccess,
        payload: response.data
      })
    }catch(error) {
      /*eslint no-console:0*/
      console.error(error)
    }
  }
};
